<template>
  <div>
    <Button  type="primary" @click="page.currPage--">上一页</Button>
    第<label><input v-model="page.showPage"></label>页, 共{{page.totalPage}}页 ,
    {{page.firstRow}}-{{page.lastRow}}/{{page.totalRow}}条
    <Button type="primary" @click="page.currPage++">下一页</Button>
  </div>
</template>

<script>
  export default {
    name: "pageTool",
    props: {
      pageInit: {}
    },
    data() {
      return {
        page: {
          "_currPage": 0,
          "_showPage": 1,
          "totalPage": this.totalPage,
          "totalRow": this.totalRow,
          "pageSize": 30,
          set currPage(val) {
            if (val >= 0 && val < this.totalPage) {
              this._currPage = val;
              this._showPage = val + 1;
            }
          },
          set showPage(val) {
            this._showPage = val;
            this._currPage = val - 1;
          },
          get showPage() {
            return this._showPage;
          },
          get currPage() {
            return this._currPage;
          },
          get firstRow() {
            return this.pageSize * this._currPage + 1;
          },
          get lastRow() {
            if (this.pageSize * (this._currPage + 1) > this.totalRow)
              return this.totalRow;
            return this.pageSize * (this._currPage + 1);
          },
        }
      }
    },
    watch: {
      pageInit:{
        handler(newVal){
          this.page.totalPage=newVal.totalPage;
          this.page.totalRow=newVal.totalRow;
        },
        deep:true,
      },
      'page.currPage' (newVal) {
        this.$emit('query',newVal);
      }
    },
    created(){
      this.page.totalPage=this.pageInit.totalPage;
      this.page.totalRow=this.pageInit.totalRow;
    }
  }
</script>

