<template>
      <Table border :columns="columns" :data="data" size="small" max-height="750">
        <template slot-scope="{ row }" slot="action">
          <Button type="primary" size="small" style="margin-right: 5px" @click="show(row.id)">View</Button>
        </template>
      </Table>
</template>

<script>
  export default {
    name: "tableTool",
    props: {
      columns: {},
      data: {},
      selectForm: {
        default() {
          return {};
        }
      }
    },
    methods: {
      show(index) {
        alert(index);
      },
    },
    watch: {
      selectForm: {
        handler() {
          this.$emit('query', this.selectForm);
        },
        deep: true,
      }
    }
  }
</script>

<style>
  Table{
    font-size: 14px;
    line-height: 1;
  }
  .ivu-table-cell{
    padding-left: 2px;
    padding-right: 2px;
  }
</style>