<template>
  <div class="login">
    <div v-if="!isLogin" class="notLogin">
      <br><br><br><br><br><br><br><br>
      <Row type="flex" justify="center" align="middle" class="code-row-bg">
        <i-col><h1>登陆</h1></i-col>
      </Row>
      <Row type="flex" justify="center" align="middle" class="code-row-bg">
        <i-col><br><br>
          <i-input size="large" v-model="username" style="width: 300px" placeholder="用户名"></i-input>
          <br><br>
          <i-input size="large" v-model="password" style="width: 300px" placeholder="密码" type="password"></i-input>
          <br><br>
          <i-button type="success" style="float:left" @click="login">登陆</i-button>
          <i-button type="error" style="float:right">清空</i-button>
        </i-col>
      </Row>
      <br><br><br>
    </div>
    <div v-else class="hadLogin">
      <br><br><br><br><br><br><br><br>
      <Row type="flex" justify="center" align="middle" class="code-row-bg">
        <i-col><h1>当前用户:{{username}}</h1></i-col>
      </Row>
      <br><br><br>
      <Row type="flex" justify="center" align="middle" class="code-row-bg">
        <i-col>
          <i-button type="error" @click="logout">注销</i-button>
        </i-col>
      </Row>
      <br><br><br>
    </div>
  </div>
</template>

<script>
  import axios from "axios";

  export default {
    name: "login",
    props: {},
    data() {
      return {
        username: '',
        password: '',
        isLogin: false,
      }
    }, methods: {
      login: function () {
        let url = 'http://localhost:1030/public/login?username=' + this.username + '&password=' + this.password;
        axios.get(url).then(res => {
          if (res.data === true) this.isLogin = true;
          else alert('用户名或密码错误!');
        }).catch(res => {
          console.log(res)
        });
      },
      logout: function () {
        let url = 'http://localhost:1030/public/logout';
        axios.get(url).then(() => {
          this.isLogin = false;
        });
      },
      getLoginState: function () {
        let url = 'http://localhost:1030/public/refresh';
        axios.get(url).then(res => {
          this.isLogin = res.data;
          let user = this.getCookie('username');
          this.username = user === false ? '' : user;
        });
      },
      getCookie: function (key) {
        let vars = document.cookie.split('; ');
        return this.getVariable(vars, key);
      },
      getVariable: function (vars, key) {
        for (let i = 0; i < vars.length; i++) {
          let pair = vars[i].split("=");
          if (pair[0] === key) {
            return pair[1];
          }
        }
        return false;
      },
    },
    created() {
      this.getLoginState();
    }
  }
</script>

